#nm0 {
  grid-area: nm0; }

#nm1 {
  grid-area: nm1; }

#nm2 {
  grid-area: nm2; }

#nm3 {
  grid-area: nm3; }

#nm4 {
  grid-area: nm4; }

#nm5 {
  grid-area: nm5; }

#nm6 {
  grid-area: nm6; }

#nm7 {
  grid-area: nm7; }

#nm8 {
  grid-area: nm8; }

#nm9 {
  grid-area: nm9; }

#alc {
  grid-area: alc; }

#cen {
  grid-area: cen; }

#div {
  grid-area: div; }

#mul {
  grid-area: mul; }

#min {
  grid-area: min; }

#pls {
  grid-area: pls; }

#eql {
  grid-area: eql; }

#dot {
  grid-area: dot; }

button:focus {
  outline: none; }

.grid-item {
  border: none;
  font-size: 36px;
  border-radius: 10px;
  background-color: #ebebeb;
  border-bottom: 2px solid #b8b8b8;
  border-right: 2px solid #b8b8b8; }

.grid-item:active {
  border: none;
  margin-top: 3px;
  margin-left: 3px;
  position: relative; }

.clear {
  color: #eee;
  font-size: 24px;
  background-color: #e02715;
  border-bottom: 2px solid #c92313;
  border-right: 2px solid #c92313; }

.grid-container {
  display: grid;
  grid-gap: 10px;
  grid-template-areas: "alc cen div mul" "nm7 nm8 nm9 min" "nm4 nm5 nm6 pls" "nm1 nm2 nm3 eql" "nm0 nm0 dot eql";
  grid-template-rows: repeat(5, 54px);
  grid-template-columns: repeat(4, 22%); }

#pad {
  padding-top: 10px; }



